.header-velcom-logo, .header-viber-logo {
  width: 60px;
  height: 60px;
}

.App-header-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.HeaderBaner {
  height: 900px;
  width: 100%;
  background: url(../assets/headerImg1.jpg) 100% /cover no-repeat;
  position: relative;
}

.HeaderBaner:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.6);
  z-index: 2;
}

.HeaderBanerIn {
  position: relative;
  z-index: 3;
  color: #fff;
  padding: 50px;  
}

.header-velcom-logo, .header-viber-logo {
  width: 18px;
  height: 18px;
};

.header-nav {
  display: flex;
  a {
    margin: 0 15px;
  }
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  z-index: 10;
  top: 0;
  background-color: #fff;
  height: 50px;
}
